"use client";

import { Bonus } from "@/lib/data";
import { BonusCard } from "./bonus-card";
import { Button } from "@/components/ui/button";
import { SlidersHorizontal, Sparkles } from "lucide-react";

interface BonusListProps {
  bonuses: Bonus[];
}

export function BonusList({ bonuses }: BonusListProps) {
  return (
    <section className="space-y-8 py-12 bg-black">
      <div className="sticky top-16 z-40 -mx-4 px-4 py-4 backdrop-blur-xl bg-black/50 border-b border-white/5 md:static md:bg-transparent md:border-none md:p-0 md:mx-0">
        <div className="flex flex-col gap-4 md:flex-row md:items-center md:justify-between">
          <div className="flex items-center gap-2">
            <div className="flex h-8 w-8 items-center justify-center rounded-lg bg-purple-500/20">
              <Sparkles className="h-4 w-4 text-purple-400" />
            </div>
            <div>
              <h2 className="text-2xl font-bold tracking-tight text-white">Top Casino Bonuses</h2>
              <p className="text-xs text-gray-500">Curated offers just for you</p>
            </div>
          </div>
          
          <div className="flex items-center gap-2 overflow-x-auto pb-2 md:pb-0 no-scrollbar">
            <Button size="sm" className="rounded-full bg-white text-black hover:bg-gray-200 font-bold">
              All Offers
            </Button>
            <Button variant="ghost" size="sm" className="rounded-full text-gray-400 hover:text-white hover:bg-white/5">
              Exclusive
            </Button>
            <Button variant="ghost" size="sm" className="rounded-full text-gray-400 hover:text-white hover:bg-white/5">
              No Deposit
            </Button>
            <Button variant="ghost" size="sm" className="rounded-full text-gray-400 hover:text-white hover:bg-white/5">
              Free Spins
            </Button>
            <div className="h-6 w-px bg-white/10 mx-2" />
            <Button variant="outline" size="icon" className="rounded-full border-white/10 bg-transparent text-gray-400 hover:text-white hover:bg-white/5 shrink-0">
              <SlidersHorizontal className="h-4 w-4" />
            </Button>
          </div>
        </div>
      </div>
      
      <div className="grid gap-6">
        {bonuses.map((bonus) => (
          <BonusCard key={bonus.id} bonus={bonus} />
        ))}
      </div>
    </section>
  );
}
